.radio {
    appearance: none;
    width: 20px;
    height: 20px;
    border:2px solid black;
    border-radius: 50%;
    position: relative;
    cursor:pointer;
    accent-color: black;
    margin-right: 10px;
    /*transform: scale(1.5);*/
}



.radio::before {
    content: '';
    /*display: block;*/
    width: 11px; /* Set size */
    height: 11px; /* Set size */
    background-color: transparent; /* Background color */
    border-radius: 50%;
    position: absolute;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
}


.radio:checked::before {
   background-color: black;
}

.label {
    display: flex;
    position: relative;
    cursor: pointer;

}

.options {
    display: flex;
    flex-direction: column;
    gap: 22px;
}

.span {
    font-family: Montserrat, sans-serif;
}