.input:focus {
    border: 1px solid mediumaquamarine;
}

.input {
    margin-bottom: 10px;
    width: 372px;
    height: 36px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;

    background: #f5f7fb;
    padding: 8px 12px;

    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #000;
    outline: none;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;
    gap: 5px;
}

.superInput {
    color: blue;
}

.errorInput {
    margin-bottom: 10px;
    width: 372px;
    height: 36px;
    border: 1px solid #c00;
    border-radius: 5px;

    background: #f5f7fb;
    padding: 8px 12px;

    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #000;
    /*color: blue;*/
    outline: none;
}

.errorInput:focus {
    border: 2px solid #cc1439;
}

.error {
    height: 10px;
    display: inline-block;
    font-weight: 400;
    font-size: 10px;
    color: #cc1439;

}
