.greetingForm {

}


.inputAndButtonContainer {
    display: flex;
    align-items: flex-start;
}

.error {
    font-family: Montserrat, sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #c00;
    height: 21px;
}

.input {
    margin: 10px 10px 10px 0;
    width: 372px;
    height: 36px;
    border: 1px solid #d1d1d1;
    border-radius: 5px;

    background: #f5f7fb;
    padding: 8px 12px;

    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #000;
    outline: none;

}

.input:focus {
    outline: none;
    border: mediumaquamarine solid 1px;
}

.errorInput {
    margin: 10px 10px 10px 0;
    width: 372px;
    height: 36px;
    border: 1px solid #c00;
    border-radius: 5px;

    background: #f5f7fb;
    padding: 8px 12px;

    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 16px;
    color: #000;
    outline: none;
}

.errorInput:focus {
    border: 1px solid mediumaquamarine;
}

.button {
    border-radius: 3px;
    border:none;
    padding: 5px 24px;
    width: 78px;
    height: 36px;
    background: #06c;
    margin-top: 10px;

    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;

    /*outline: none;*/
}

.button:focus {
    outline: #99ff99 solid 1px;
}

.button:active {
    background: #4293e3;
}

.button:disabled {
    pointer-events: none; /* Prevent click and hover effects */
    opacity: 0.5;
}

.text {
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
}

.greeting {
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #000;

}
