.button {
    border-radius: 3px;
    border:none;
    padding: 5px 24px;
    width: 132px;
    height: 30px;

    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    white-space: nowrap;


    /*outline: none;*/
}

.disabled {
    background: #004d99;
    opacity: 0.5;
    color: #002e5c;
}

.secondary {
    border: 1px solid #06c;
    width: 176px;
    color: #06c;
    background-color: transparent;
}

.default {
    background: #06c;
    color: #fff;
}

.red {
    background: #cc1439;
    color: #fff;
}

.default:hover {
    background: #0080ff;
}

.red:hover {
    background: #ff1a47;
}

.secondary:hover {
    background: #e5f0fa;
}


.default:active {
    background: #0059b2;
}

.secondary:active {
    background: #c9e5ff;
}

.red:active {
    background: #b21232;
}

.button:disabled {
}
