.sidebar {
    display: flex;
    flex-direction: column;
    position: fixed;
    top: 0;
    left: -317px; /* Initially hidden */
    width: 317px;
    height: 100%;
    /*background-color: #444;*/
    color: #fff;
    z-index: 15; /* Higher z-index to overlap the header */
    transition: all 0.3s ease-in-out;
    /*padding: 20px;*/
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 10px 40px 0 rgba(29, 33, 38, 0.13);
    background: #fff;
    padding: 16px 24px;
    gap: 24px;

}

/*.sidebar > :first-child {*/
/*    margin-left: 250px; !* Example: Adds space from the left *!*/
/*    margin-right: 24px;*/
/*}*/


.sidebar.open {
    left: 0; /* Slide in */
}

.background {
    background: #000;
    opacity: 0.3;
    /*width: 100%;*/
    /*height: 500px;*/
    z-index: 10;
    position: fixed;
    /*top: 0;*/
    /*left: 250px; !* Ensures the background starts from the right edge of the menu *!*/
    height: 100%;
    /*width: calc(100% - 250px); !* Adjust this width based on the sidebar's size *!*/
    width: 100%;

    /*pointer-events: none; !* Prevent interaction when hidden *!*/
    transition: opacity 4.3s ease;
}

.nav {
    display: flex;
    flex-direction: column;
    /*justify-content: center;*/
    /*align-items: center;*/
    margin-left: 46px;
    gap: 45px;
}

.nav a {
    text-decoration: none;
    font-family: Montserrat, sans-serif;
    font-weight: 600;
    font-size: 22px;
    color: #000;
}

.nav a:hover {
    color: #58acff;
}

.nav a.active {
    text-decoration: underline;
    /*color: indianred;*/
    text-decoration-skip-ink: none;
    color: #0059b2;
}
.close {
    position: relative;
    display: flex;
    /*align-items: flex-end;*/
    background-color: transparent;
    border: none;
    align-self: end;
    /*margin-bottom: 24px;*/
    /*left: 120px;*/
}

.close img {
    cursor: pointer;
}
